import React from 'react';
import HeaderContainer from "./HeaderContainer";
import styles from './../style/NewsPage.module.css'
import NewsContent from "./NewsContent";
import PaginatorNews from "./PaginatorNews";
import AsideContainer from "./AsideContainer";

const arrNews = [
    {id: 1, name: ' '},
    {id: 2, name: ' '}, {id: 3, name: '  '},
    {id: 4, name: ' '}, {id: 5, name: ' '}
    , {id: 6, name: '    5/2'}, {id: 7, name: ' '},
    {id: 8, name: '  '}, {id: 9, name: '  '}]

const NewsPage = () => {
    return (
        <div className={styles.wrapper}>
            <div className={styles.header}>
                <HeaderContainer title={''} activeLink={''}/>
            </div>
            <div className={styles.wrapper__container}>
                <div className={styles.wrapper__container__wrap}>
                    <AsideContainer/>
                    <div className={styles.wrapper__container__wrap__info}>
                        <div className={styles.wrapper__container__content}>
                            {arrNews.map(news => {
                                return <NewsContent news={news} key={news.id}/>
                            })}
                        </div>
                        <PaginatorNews/>
                        <div className={styles.line}></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default NewsPage